<style lang="less" scoped>
@import "../../css/common.less";
.anyi-slide {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}
.anyi-slide-content {
  width: 100%;
  flex: 1;
  overflow-y: hidden;
  position: relative;
  overflow: hidden;
  position: absolute;
}

.anyi-tab-content-scroll {
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
</style>
<template>
    <div class="anyi-slide">
        <div @scroll.stop.prevent="itemScroll" :class="{'anyi-slide-content-scroll': isScroll}" class="anyi-slide-content" @touchstart="touchstart" @touchmove="touchmove" @touchend="touchend">
            <slot></slot>
        </div>
    </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  props: {
    isScroll: {
      type: Boolean,
      default: true
    }
  },
  watch: {},
  computed: {},
  mounted() {},
  methods: {
    touchstart(e) {
      if (!this.isScroll) {
        e.preventDefault();
        e.stopPropagation();
      }
    },
    touchmove(e) {
      if (!this.isScroll) {
        e.preventDefault();
        e.stopPropagation();
      }
    },
    touchend(e) {
      if (!this.isScroll) {
        e.preventDefault();
        e.stopPropagation();
      }
    },
    //当内容滚动到头部时开启touch
    itemScroll(e) {}
  }
};
</script>





