<style scoped lang="less">
@import "../css/common.less";
.container {
  background: #fff;
  overflow: hidden;
  .content {
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
    .slide-top {
      .slide-img {
        margin: auto;
        margin-top: unit(50/@rem, rem);
        height: unit(240/@rem, rem);
        width: unit(240/@rem, rem);
        border: 2px solid #d0d0d0;
        border-radius: 100%;
      }
      .title {
        text-align: center;
        padding: unit(10/@rem, rem);
      }
    }
    .items {
      padding: 0 unit(50/@rem, rem);
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      .item {
        flex-direction: row;
        .content {
          flex: 0 1 auto;
          padding: unit(30/@rem, rem) unit(20/@rem, rem);
          color: #333;
          margin-bottom: unit(40/@rem, rem);
          background: #e0e0e0;
        }
        .block {
          flex: 1;
        }
      }
      .left {
        margin-right: unit(100/@rem, rem);
      }
      .right {
        justify-content: flex-end;
      }
    }
  }

  .footer {
    flex: 0 0 auto;
    background: #e0e0e0;
    padding: unit(20/@rem, rem) unit(40/@rem, rem);
    height: unit(120/@rem, rem);
    input {
      margin-left: unit(40/@rem, rem);
      height: unit(80/@rem, rem);
      line-height: unit(80/@rem, rem);
      border: 1px solid #d0d0d0;
      border-radius: unit(40/@rem, rem);
      background: #fff;
      text-indent: unit(50/@rem, rem);
    }
  }
}
</style>
<template>
  <div class="container flex" ref="container">
    <div class="content" ref="content">
      <div class="slide-top">
        <div class="slide-img">
        </div>
        <h2 class="title">jiajia</h2>
      </div>
      <ul class="items">
        <li class="item left flex">
          <div class="content">
            Hi,我是你的私人风险管家,我可以为你提供风险预测,风险建议等服务,我知道的可多了,有什么问题直接问我就好了
          </div>
          <div class="block">
          </div>
        </li>
        <li class="item left flex">
          <div class="content">
            Hi,我是你的私人风险管家,我可以为你提供风险预测,风险建议等服务,我知道的可多了,有什么问题直接问我就好了
          </div>
          <div class="block">
          </div>
        </li>
        <li class="item right flex">
          <div class="block">
          </div>
          <div class="content">
            如果你不知道怎么问, 风险管家,我可以为你提
          </div>
        </li>
      </ul>
      <ul class="items">
        <li class="item left flex">
          <div class="content">
            Hi,我是你的私人风险管家,我可以为你提供风险预测,风险建议等服务,我知道的可多了,有什么问题直接问我就好了
          </div>
          <div class="block">
          </div>
        </li>
        <li class="item left flex">
          <div class="content">
            Hi,我是你的私人风险管家,我可以为你提供风险预测,风险建议等服务,我知道的可多了,有什么问题直接问我就好了
          </div>
          <div class="block">
          </div>
        </li>
        <li class="item right flex">
          <div class="block">
          </div>
          <div class="content">
            如果你不知道怎么问, 风险管家,我可以为你提
          </div>
        </li>
      </ul>
    </div>
    <div class="footer flex row c-center" ref="inputWrap">
      <span class="f-0">
        展开
      </span>
      <input @focus="inputFocus" @focusin="focusin" @focusout="focusout" type="text" class="f-1" placeholder="请输入要问的问题">
    </div>
  </div>
</template>
<script>
import slideView from "../components/common/slideView";
import anyiTab from "../components/common/anyiTab";
export default {
  components: {
    slideView,
    anyiTab
  },
  data() {
    return {
      footer: "50px",
      isFocus: false
    };
  },
  mounted() {
    this.$refs.content.addEventListener("touchmove", e => {
      if (this.isFocus) {
        e.preventDefault();
        e.stopPropagation();
      }
    });
  },
  methods: {
    focusin(e) {
      this.isFocus = true;
    },
    focusout(e) {
      this.isFocus = false;
    },
    inputFocus() {
      this.$refs.content.scrollTop = 10000000;
    }
  }
};
</script>



